import axios from 'axios'
import storage from '../utils/Storage'

const baseUrl = '/api/classifieds'

const getConfig = () => {
    return {
      headers: { Authorization: `bearer ${storage.loadUser().token}` }
    }
}

const getAll = async () => {
    try {
      const response = await axios.get(baseUrl)

      return response.data
    }
    catch (exception) {
      console.log('exception : ', exception.message)
      return exception
    }
  } 

const getClassified = async (classifiedID) => {
    try {
        const response = await axios.get(`${baseUrl}/${classifiedID}`)
        
        return response.data
    }
    catch (exception) {
        console.log('exception : ', exception.message)
        return exception
    }  
}

const createClassified = async (classified) => {
    try {
        const request = axios.post(baseUrl, classified, getConfig())

        return request.then(response => response.data)
    }
    catch (exception) {
        console.log('exception : ', exception.message)
        return exception
    }  
}

const updateClassified = async (classified) => {
    try {
        const request = axios.put(`${baseUrl}/${classified.id}`, classified, getConfig())

        return request.then(response => response.data)
    }
    catch (exception) {
        console.log('exception : ', exception.message)
        return exception
    }  
}

const deleteClassified = async (id) => {
    try {
        const request = axios.delete(`${baseUrl}/${id}`, getConfig())

        return request.then(response => response.data)
    }
    catch (exception) {
        console.log('exception : ', exception.message)
        return exception
    }  
}

export default { getAll, getClassified, createClassified, updateClassified, deleteClassified }