import React, { useState, useEffect } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { Table, Form, Button, Alert } from 'react-bootstrap'
import { useLocation, useHistory } from 'react-router-dom';
import storage from '../utils/Storage'
import moment from 'moment'
import { getCandidateDetails } from '../reducers/candidateReducer'
import candidateService from '../services/candidateService'
import shortlistService from '../services/shortlistService'

const ViewCandidateForm = () => {

    const dispatch = useDispatch()  
    const location = useLocation()
    const history = useHistory() 

    const [selectedCandidate, setSelectedCandidate] = useState([]) 

    let selectedCandidateID = null
    let loggedinCandidateID = null

    if (location.data !== undefined)
    {
        selectedCandidateID = location.data.candidateID
        // console.log(selectedCandidateID)
        loggedinCandidateID = location.data.loggedinCandidateID    
        // console.log(loggedinCandidateID)
    }
   
    // let selectedCandidateDetails = useEffect(() => { dispatch(getCandidateDetails(selectedCandidateID)) })
    // console.log('selectedCandidateDetails 1 : ', selectedCandidateDetails)
    // selectedCandidateDetails = useSelector(state => state.candidateDetails)    
    // console.log('selectedCandidateDetails 2 : ', selectedCandidateDetails)

    useEffect(() => { candidateService.getCandidateDetails(selectedCandidateID === undefined ? "" : selectedCandidateID)
                                        .then(selectedCandidate => { setSelectedCandidate(selectedCandidate) }) }, [selectedCandidateID])
    
    const shortlistCandidate = (event) => {
        try{
            event.preventDefault()

            if (loggedinCandidateID === "undefined" || loggedinCandidateID === undefined)
            {
                window.alert("You need to enter your candidate details first before shortlisting someone else.")
                history.push({ pathname: `/Matrimonialform/${loggedinCandidateID}`, data: {id: loggedinCandidateID} })
                // history.push({ pathname: `/dashboard/${loggedinCandidateID}`, data: {id: loggedinCandidateID} })
            }
            else
            {
                const shortlistCandidate = {loggedinCandidateID, selectedCandidateID}
                const candidateToBeShortlisted = shortlistService.createShortlistCandidates(shortlistCandidate)
                console.log('value of candidateToBeShortlisted : ', candidateToBeShortlisted)
            }            
        }
        catch(exception)
        {
            console.log(exception)
        }
    }

    const BackToDashboard = (candidateID) => {     
        try
        {
            if (candidateID === null || candidateID === undefined)
            {
                let user = storage.loadUser() 

                if (user !== null & user !== undefined)
                {
                    candidateID = user.candidateid    
                }
            }

            history.push({ pathname: `/dashboard/${candidateID}`, data: {id: candidateID} })
        }
        catch(exception)
        {
            console.log(exception)
        }
    }

    if (selectedCandidateID !== null & selectedCandidateID !== undefined)
    {
        return (
            <div className="components">                             
                <Form onSubmit={shortlistCandidate}>
                    <Table striped bordered>                                                       
                        <thead>
                            <tr>
                                <th colSpan="5"><b>Candidate Matrimonial Details</b></th>
                            </tr>
                        </thead>
                        <tbody>    
                            <tr>
                                <td>                             
                                    <Form.Text muted><b>Full Name :</b></Form.Text>                        
                                    <Form.Control id="fullname" name="fullname" value={selectedCandidate.fullname} readOnly/>
                                    <p />
                                    <Form.Text muted><b>Gender :</b></Form.Text>
                                    <Form.Control id="gender" name="gender" value={selectedCandidate.gender} readOnly/>
                                    <p />
                                    <Form.Text muted><b>Birth Date :</b></Form.Text>                                    
                                    <Form.Control id="birthdate" name="birthdate" value={moment(selectedCandidate.birthdate).format('DD-MMM-YYYY')} readOnly/>                                    
                                    <p />
                                    <Form.Text muted><b>Birth Time :</b></Form.Text>
                                    <Form.Control id="birthtime" name="birthtime" value={selectedCandidate.birthtime} readOnly/>
                                    <p />
                                    <Form.Text muted><b>Birth Place :</b></Form.Text>
                                    <Form.Control id="birthplace" name="birthplace" value={selectedCandidate.birthplace} readOnly/>                                    
                                    <p />
                                    <Form.Text muted><b>Native :</b></Form.Text>
                                    <Form.Control id="native" name="native" value={selectedCandidate.native} readOnly/>
                                    <p />
                                    <Form.Text muted><b>Mosal :</b></Form.Text>
                                    <Form.Control id="mosal" name="mosal" value={selectedCandidate.mosal} readOnly/>
                                    <p />
                                    <Form.Text muted><b>Marraige Status :</b></Form.Text>
                                    <Form.Control id="marriagestatus" name="marriagestatus" value={selectedCandidate.marriagestatus} readOnly/>
                                    <p />                                                                             
                                    <Form.Text muted><b>Horoscope :</b></Form.Text>      
                                    <Form.Control id="horoscope" name="horoscope" value={selectedCandidate.horoscope} readOnly/>
                                </td>                    
                                <td>
                                    <Form.Text muted><b>Mobile No :</b></Form.Text>
                                    <Form.Control id="mobileno" name="mobileno" value={selectedCandidate.mobileno} readOnly/>
                                    <p />                             
                                    <Form.Text muted><b>Height (cms) :</b></Form.Text>
                                    <Form.Control id="height" name="height" value={selectedCandidate.height} readOnly/>
                                    <p />
                                    <Form.Text muted><b>Weight (kgs) :</b></Form.Text>
                                    <Form.Control id="weight" name="weight" value={selectedCandidate.weight} readOnly/>
                                    <p />
                                    <Form.Text muted><b>Education :</b></Form.Text>
                                    <Form.Control id="education" name="education" value={selectedCandidate.education} readOnly/>
                                    <p />
                                    <Form.Text muted><b>Profession :</b></Form.Text>
                                    <Form.Control id="profession" name="profession" value={selectedCandidate.profession} readOnly/>
                                    <p />
                                    <Form.Text muted><b>Email :</b></Form.Text>
                                    <Form.Control id="email" name="email" value={selectedCandidate.email} readOnly/>
                                    <p />
                                    <Form.Text muted><b>Address :</b></Form.Text>
                                    <Form.Control id="address" name="address" value={selectedCandidate.address} readOnly/>
                                    <p />
                                    <Form.Text muted><b>City :</b></Form.Text>
                                    <Form.Control id="city" name="city" value={selectedCandidate.city} readOnly/>
                                    <p />
                                    <Form.Text muted><b>Country :</b></Form.Text>
                                    <Form.Control id="country" name="country" value={selectedCandidate.country} readOnly/>
                                    <p />                       
                                </td>
                                <td>
                                <Form.Text muted><b>Father Name :</b></Form.Text>
                                    <Form.Control id="fathername" name="fathername" value={selectedCandidate.fathername} readOnly/>
                                    <p />
                                    <Form.Text muted><b>Mother Name :</b></Form.Text>
                                    <Form.Control id="mothername" name="mothername" value={selectedCandidate.mothername} readOnly/>                          
                                    <Form.Text muted><b>Sibling 1 :</b></Form.Text>
                                    <Form.Control id="sibling1" name="sibling1" value={selectedCandidate.sibling1} readOnly/>
                                    <p />
                                    <Form.Text muted><b>Relation 1 :</b></Form.Text>
                                    <Form.Control id="relation1" name="relation1" value={selectedCandidate.relation1} readOnly/>
                                    <p />            
                                    <Form.Text muted><b>Sibling 2 :</b></Form.Text>
                                    <Form.Control id="sibling2" name="sibling2" value={selectedCandidate.sibling2} readOnly/>
                                    <p />
                                    <Form.Text muted><b>Relation 2 :</b></Form.Text>
                                    <Form.Control id="relation2" name="relation2" value={selectedCandidate.relation2} readOnly/>
                                    <p />                        
                                    <Form.Text muted><b>Sibling 3 :</b></Form.Text>
                                    <Form.Control id="sibling3" name="sibling3" value={selectedCandidate.sibling3} readOnly/>
                                    <p />
                                    <Form.Text muted><b>Relation 3 :</b></Form.Text>
                                    <Form.Control id="relation3" name="relation3" value={selectedCandidate.relation3} readOnly/>                        
                                    <p />                                         
                                </td>
                                <td>
                                                                               
                                    <Form.Text muted><b>Relative 1 :</b></Form.Text>
                                    <Form.Control id="relative1" name="relative1" value={selectedCandidate.relative1} readOnly/>
                                    <p />
                                    <Form.Text muted><b>Contact 1 :</b></Form.Text>
                                    <Form.Control id="contact1" name="contact1" value={selectedCandidate.contact1} readOnly/>
                                    <p />
                                    <Form.Text muted><b>Relative 2 :</b></Form.Text>
                                    <Form.Control id="relative2" name="relative2" value={selectedCandidate.relative2} readOnly/>
                                    <p />
                                    <Form.Text muted><b>Contact 2 :</b></Form.Text>
                                    <Form.Control id="contact2" name="contact2" value={selectedCandidate.contact2} readOnly/>
                                    <p />
                                    <Form.Text muted><b>Relative 3 :</b></Form.Text>
                                    <Form.Control id="relative3" name="relative3" value={selectedCandidate.relative3} readOnly/>
                                    <p />
                                    <Form.Text muted><b>Contact 3 :</b></Form.Text>
                                    <Form.Control id="contact3" name="contact3" value={selectedCandidate.contact3} readOnly/>
                                    <p />
                                    <Form.Text muted><b>Candidate Photo :</b></Form.Text>                                    
                                    <img id="CandidatePhoto" src={selectedCandidate.photo} alt="CandidatePhoto" height="125" width="125"/>                    
                                </td>
                                <td>                           
                                    <Form.Text muted><b>Relative 4 :</b></Form.Text>
                                    <Form.Control id="relative4" name="relative4" value={selectedCandidate.relative4} readOnly/>
                                    <p />
                                    <Form.Text muted><b>Contact 4 :</b></Form.Text>
                                    <Form.Control id="contact4" name="contact4" value={selectedCandidate.contact4} readOnly/>
                                    <p />
                                    <Form.Text muted><b>Relative 5 :</b></Form.Text>
                                    <Form.Control id="relative5" name="relative5" value={selectedCandidate.relative5} readOnly/>
                                    <p />
                                    <Form.Text muted><b>Contact 5 :</b></Form.Text>
                                    <Form.Control id="contact5" name="contact5" value={selectedCandidate.contact5} readOnly/>
                                    <p />
                                    <Form.Text muted><b>Relative 6 :</b></Form.Text>
                                    <Form.Control id="relative6" name="relative6" value={selectedCandidate.relative6} readOnly/>
                                    <p />
                                    <Form.Text muted><b>Contact 6 :</b></Form.Text>
                                    <Form.Control id="contact6" name="contact6" value={selectedCandidate.contact6} readOnly/>
                                    <p />
                                    <Form.Text muted><b>Remarks :</b></Form.Text>
                                    <Form.Control id="remarks" name="remarks" value={selectedCandidate.remarks} defaultValue="remarks"/>
                                    <p />                        
                                    <td>
                                    <Button id="shortlist" type="submit">Shortlist</Button>
                                    {" "}
                                    <Button id="back" onClick={() => BackToDashboard(loggedinCandidateID)}>Back</Button>
                                    </td>
                                    <p />
                                </td>
                            </tr>
                        </tbody>                    
                </Table>
            </Form>
        </div>
        )
    }
    else
    {
        return (
            <div>
                Loading ...
            </div>
        )
    }    
}

export default ViewCandidateForm