const storageUserKey = 'loggedMatrimonialAppUser'
const storagAvailableCandidateCountKey = 'availableCandidateCount'
const storagNewCandidateID = 'newCandidateID'

const saveUser = (user) =>  
  localStorage.setItem(storageUserKey, JSON.stringify(user))

const loadUser = () =>
  JSON.parse(localStorage.getItem(storageUserKey))

const logoutUser = () =>
  localStorage.removeItem(storageUserKey)  

const saveCandidateCount = (candidateCount) => 
  localStorage.setItem(storagAvailableCandidateCountKey, candidateCount)

const loadCandidateCount = () => 
  localStorage.getItem(storagAvailableCandidateCountKey)

const removeCandidateCount = () => 
  localStorage.removeItem(storagAvailableCandidateCountKey)

const loadNewCandidateID = () => 
  localStorage.getItem(storagNewCandidateID)

export default { saveUser, loadUser, logoutUser, saveCandidateCount, loadCandidateCount, removeCandidateCount, loadNewCandidateID}