import initializeService from '../services/initializeService'

const initializeReducer = (state = [], action) => {
    switch (action.type) {
        case 'INIT_EDUCATIONS':
            // console.log('INIT_EDUCATIONS : ', action.data)
            return action.data
        case 'INIT_PROFESSIONS':
            // console.log('INIT_PROFESSIONS : ', action.data)
            return action.data
        case 'INIT_CITIES':
            // console.log('INIT_CITIES : ', action.data)
            return action.data
        case 'INIT_COUNTRIES':
            // console.log('INIT_COUNTRIES : ', action.data)
            return action.data
        case 'INIT_GENDERS':
            // console.log('INIT_GENDERS : ', action.data)
            return action.data
        case 'INIT_MARRIAGESTATUSES':
            // console.log('INIT_MARRIAGESTATUSES : ', action.data)
            return action.data
        case 'INIT_HOROSCOPES':
            // console.log('INIT_HOROSCOPES : ', action.data)
            return action.data
        default:
            return state
    }
}

export const initializeEducations = () => {
    return async dispatch => {
        const educations = await initializeService.getEducation()
        
        dispatch({
            type: 'INIT_EDUCATIONS',
            data: educations
        })
    }
}

