import React, { useEffect, useState, Fragment } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { BrowserRouter as Router, Switch, Route, Link } from 'react-router-dom'
import { Form, Button, Navbar, Nav, FormControl, Table } from 'react-bootstrap'

import Dashboard from './components/Dashboard'
import MatrimonialForm from './components/MatrimonialForm'
import SearchForm from './components/SearchForm'
import ViewCandidateForm from './components/ViewCandidateForm'
import DirectoryForm from './components/DirectoryForm'
import Classifieds from './components/Classifieds'
import Footer from './components/Footer'
import storage from './utils/Storage'
import './App.css'

import { initializeCandidates, getCandidateDetails } from './reducers/candidateReducer'
import { userLogin, userLogout } from './reducers/loginReducer'
import { initializeUsers } from './reducers/userReducer'
import { displayNotification, hideNotification } from './reducers/notificationReducer'
import loginService from './services/loginService'
import userService from './services/userService'

const App = () => {
  
  const dispatch = useDispatch()  
  
  const [showRegister, setShowRegister] = useState(false)
  const [username, setUsername] = useState(null)
  const [password, setPassword] = useState(null)
  const [name, setName] = useState(null)
  const [phone, setPhone] = useState(null)
  const [email, setEmail] = useState(null)

  const padding = {
    padding: 5
  }
  
  // let username = ''
  // let password = ''  
  let candidateid = ''
  let loginid = ''

  let user =  useSelector(state => state.login)
  
  // This is to get all available candidates in application.
  let candidates = useEffect(() => { dispatch(initializeCandidates()) },[dispatch])  
  candidates = useSelector(state => state.candidates)  

  const NotifyWith = (message, type='success') => {
    dispatch(displayNotification(message, type),
      setTimeout(() => {
        dispatch(hideNotification(null))
      }, 4000))
  }

  const sleep = (milliseconds) => {
    return new Promise(resolve => setTimeout(resolve, milliseconds))
}

  const HandleLogin = async (event) => {
    event.preventDefault()

    try {
      // username = event.target.username.value
      // event.target.username.value = ''
      // password = event.target.password.value
      // event.target.password.value = ''
      
      //user = await loginService.login({ username, password })

      if (username === null || password === null)
      {
        window.alert("You need to enter your username and password to login to application.")
      }
      else
      {
        user = dispatch(userLogin(username, password))      
      
        // If there is a successful login, then save user details in browser local storage.
        if (user !== null || user !== undefined)
        {          
          storage.saveUser(user)                  
          storage.saveCandidateCount(candidates.length)
        }
        
        if (user !== null || user.name !== undefined)
        {
          NotifyWith(`${user.name} welcome back!`)
        }
      }
    }
    catch(exception) {
      NotifyWith('wrong username/password', 'error')      
    }    
  }

  const HandleLogout = () => {        
    try
    {
      user = undefined
      dispatch(userLogout())    
    }    
    catch(exception)
    {
      console.log(exception)
    }
  }

  const ShowRegistration = () => {
    try 
    {      
      setShowRegister(true)
    }
    catch(exception)
    {
      console.log(exception)
    }
  }

  const HandleRegistration = () => {
    try 
    {      
      const newUser = {username, password, name, phone, email}      

      userService.create(newUser)      

      setShowRegister(false)

      setTimeout(()=> {
        user = dispatch(userLogin(username, password))    
      }, 2000)      

      // do {
      //   sleep(5000)
      // } while (user !== Promise)

      if (user !== null || user !== undefined)
      {
        storage.saveUser(user)        
      }
    }
    catch(exception)
    {
      console.log(exception)
    }
  }

  const updateUsername = (event) => {
    try 
    {       
      setUsername(event.target.value)
    }        
    catch(exception)
    {
        console.log(exception)
    }
  }

  const updatePassword = (event) => {
    try 
    {       
      setPassword(event.target.value)
    }        
    catch(exception)
    {
        console.log(exception)
    }
  }

  const updateName = (event) => {
    try 
    {       
      setName(event.target.value)
    }        
    catch(exception)
    {
        console.log(exception)
    }
  }

  const updatePhone = (event) => {
    try 
    {       
      setPhone(event.target.value)
    }        
    catch(exception)
    {
        console.log(exception)
    }
  }

  const updateEmail = (event) => {
    try 
    {       
      setEmail(event.target.value)
    }        
    catch(exception)
    {
        console.log(exception)
    }
  }

  // This is to get loggedin user details after a successful login from storage.
  user = storage.loadUser()   
  
  if (user !== null || user === undefined)
  {
    if (user.name === "Error")
    {
      window.alert("Please verify username and password being entered.")
      storage.logoutUser()
      user = undefined
    }
    else
    {
      candidateid = user.candidateid    
      loginid = user.loginid
    }    
  }

  //user = 'engineer1'
  
  if ( !user ) {
    return (
      <div>
        <Form onSubmit={HandleLogin}>
          <Table striped bordered>
          {/* <Table striped bordered size="sm" variant="dark"> */}
            <thead>
              <tr>
                <th><h2>Login to Matrimonial Portal</h2></th>
              </tr>              
              </thead>
            <tbody>
              <tr>
                <td><Form.Label>username:</Form.Label></td>
                <td><Form.Control id="username" name="username" 
                      value = {username} onChange = {event => updateUsername(event)}/></td>
              </tr>
              <tr>
                <td><Form.Label>password:</Form.Label></td>
                <td><Form.Control id="password" name="password"
                      value = {password} onChange = {event => updatePassword(event)}/></td>
              </tr>
              {showRegister === true ? 
              <Fragment>
                <tr>
                  <td><Form.Label>name:</Form.Label></td>
                  <td><Form.Control id="name" name="name" 
                      value = {name} onChange = {event => updateName(event)}/></td>
                </tr>
                <tr>
                  <td><Form.Label>phone:</Form.Label></td>
                  <td><Form.Control id="phone" name="phone"
                      value = {phone} onChange = {event => updatePhone(event)}/></td>
                </tr>
                <tr>
                  <td><Form.Label>email:</Form.Label></td>
                  <td><Form.Control id="email" name="email"
                      value = {email} onChange = {event => updateEmail(event)}/></td>
                </tr>
                </Fragment>
                : undefined}             
              <tr>
                <td></td>                
                <td><Button variant="primary" id="login" type="submit" margin="10px">Login</Button>
                {" "}
                {showRegister === false ? 
                  <Button variant="link" id="register" margin="10px" onClick={() => ShowRegistration()}>No UserID? Register Here...</Button>
                  : undefined}
                {showRegister === true ? 
                  <Button variant="primary" id="register" margin="10px" onClick={() => HandleRegistration()}>Register</Button>                
                  : undefined}
                </td>                
              </tr>
            </tbody>          
            </Table>
        </Form>
      </div>        
    )
}
else
{
  if (user.name === "Error")
    {
      window.alert("You need to enter your candidate details first before trying to update it.")
      storage.logoutUser()
    }
  else
    {
      return (
        <div>      
        <Router>
        <Navbar collapseOnSelect expand="lg" className="navbar">
        {/* <Navbar collapseOnSelect expand="lg" bg="warning" variant="warning"> */}
              <Navbar.Toggle aria-controls="responsive-navbar-nav" />
              <Nav className="m-auto">
                <Nav.Link href="#home">
                  <Link style={padding} to='/'>Home</Link>
                </Nav.Link>
                <Nav.Link href="#dashboard">                  
                  <Link style={padding} to={`/dashboard/${candidateid}`}>Dashboard</Link>
                  {/* {candidateid === undefined ? 
                    <Link style={padding} to={`/dashboard/${loginid}`}>Dashboard</Link> : 
                    <Link style={padding} to={`/dashboard/${candidateid}`}>Dashboard</Link>} */}
                </Nav.Link>
                <Nav.Link href="#matrimonialform">
                  <Link style={padding} to='/matrimonialform'>Matrimonial Form</Link>           
                </Nav.Link>
                <Nav.Link href="#searchform">
                  <Link style={padding} to='/searchform'>Search Profiles</Link>
                </Nav.Link>
                <Nav.Link href="#classifieds">
                  <Link style={padding} to='/classifieds'>Classifieds</Link>
                </Nav.Link>
                <Nav.Link href="#directory">
                  <Link style={padding} to='/directory'>Directory</Link>
                </Nav.Link>              
                <Nav.Link href="#" className="loggedinUser">                
                  {user ? <em>{user.name} logged in <Button variant="outline-danger" onClick={() => HandleLogout()}>logout</Button></em> : <Link to='HandleLogin'>login</Link>}                
                </Nav.Link>
              </Nav>          
            </Navbar>

            <div className="content">
            <Switch>
              <Route path="/dashboard/:id">
                <Dashboard />
              </Route>
              {/* <Route path='/dashboard'>
                <Dashboard />
              </Route> */}
              {/* <Route path='/matrimonialform/:id'>
                <MatrimonialForm />
              </Route> */}
              <Route path='/matrimonialform'>
                <MatrimonialForm />
              </Route>
              <Route path='/searchform'>
                <SearchForm />
              </Route>
              <Route path='/viewCandidateForm'>
                <ViewCandidateForm />
              </Route>
              <Route path="/shortlists/:id">
                <Dashboard />
              </Route>
              <Route path="/shortlists">
                <Dashboard />
              </Route>
              <Route path='/classifieds'>
                <Classifieds />
              </Route>
              <Route path='/directory'>
                <DirectoryForm />
              </Route>            
              <Route path='/'>
              </Route>
            </Switch>
            </div>         
            <p />
            <Footer />
        </Router>
      </div>
      )  
    }  
  }
}

export default App