import React, { useState, useEffect } from 'react'
import { Table, Form, Button } from 'react-bootstrap'
import Select from 'react-select'
import initializeService from '../services/initializeService'
import classifiedService from '../services/classifiedService'

const Classifieds = () => {
 
    const [classifieds, setClassifieds] = useState([]) 
    const [classifiedCategories, setclassifiedCategories] = useState([]) 
    const [selectedCategory, setSelectedCategory] = useState(null)
    const [category, setCategory] = useState('')

    const [description, setDescription] = useState('')
    const [name, setName] = useState('')
    const [phone, setPhone] = useState(0)
    const [address, setAddress] = useState('')
    const [expectedprice, setExpectedprice] = useState(0)

    useEffect(() => { initializeService.getClassifiedCategory()
        .then(classifiedCategories => { setclassifiedCategories(classifiedCategories) }) }, [])    

    const GetAvailableClassifieds = () => {
        useEffect(() => { classifiedService.getAll()
            .then(classifieds => { setClassifieds(classifieds) }) }, []) 
    }

    const updateClassifiedCategory = (event) => {        
        try 
        {            
            setSelectedCategory(event.value)            
            setCategory(event.value)    
        }        
        catch(exception)
        {
            console.log(exception)
        }
    }
        
    const updateDescription = (event) => {
        try
        {            
            setDescription(event.target.value)
        }        
        catch(exception)
        {
            console.log(exception)
        }
    }

    const updateName = (event) => {
        try
        {            
            setName(event.target.value)
        }        
        catch(exception)
        {
            console.log(exception)
        }
    }

    const updateAddress = (event) => {
        try
        {            
            setAddress(event.target.value)
        }        
        catch(exception)
        {
            console.log(exception)
        }
    }

    const updatePhone = (event) => {
        try
        {            
            setPhone(event.target.value)
        }        
        catch(exception)
        {
            console.log(exception)
        }
    }

    const updateExpectedprice = (event) => {
        try
        {            
            setExpectedprice(event.target.value)
        }        
        catch(exception)
        {
            console.log(exception)
        }
    }

    const clearFields = () => {
        setDescription(null)
        setName(null)
        setPhone(0)
        setAddress(null)
        setExpectedprice(0)
    }

    GetAvailableClassifieds()

    const handleNewClassified = (event) => {
        try
        {
            event.preventDefault()

            setCategory(selectedCategory)                                            
            setSelectedCategory(null)

            setDescription(event.target.description.value)
            event.target.description.value = ''

            setName(event.target.name.value)
            event.target.name.value = ''

            setPhone(event.target.phoneno.value)
            event.target.phoneno.value = ''

            setAddress(event.target.address.value)
            event.target.address.value = ''

            setExpectedprice(event.target.expectedprice.value)
            event.target.expectedprice.value = ''

            const classified = {category, description, name, phone, address, expectedprice}
            
            const classifiedToBeAdded = classifiedService.createClassified(classified)

            clearFields()
        }
        catch(exception)
        {
            console.log(exception)
        }
    }

    if (classifieds !== null)
    {
    return (        
            <div className="components"> 
                    <Form onSubmit={handleNewClassified}>
                        <Table striped bordered>
                            <thead>
                            </thead>
                            <tbody>
                                <tr>
                                    <td width="20%">
                                        <Table>
                                            <thead>
                                                <tr>                                                
                                                    <th colSpan="3">
                                                        <b>Post a Classified</b>                                                        
                                                    </th> 
                                                </tr>
                                            </thead>
                                            <tbody>
                                                <tr>
                                                    <td>
                                                        <p /> 
                                                        <Form.Text muted><b>Classified Category :</b></Form.Text>                                                                                                               
                                                        <Select options={classifiedCategories} 
                                                            value ={{ label: selectedCategory === null ? "Classified Category*" : selectedCategory, 
                                                                        value: selectedCategory }}
                                                            onChange={event => updateClassifiedCategory(event)} defaultValue={{ label: 'All', value: 'ALL' }} />                                                        
                                                        <p />
                                                        <Form.Text muted><b>Description :</b></Form.Text>                        
                                                        <Form.Control id="description" name="description" as="textarea" value = {description}
                                                            placeholder="Description*" required  rows={6} onChange = {event => updateDescription(event)}/>
                                                        <p />
                                                        <Form.Text muted><b>Name :</b></Form.Text>
                                                        <Form.Control id="name" name="name" value = {name}
                                                            placeholder="Name*" onChange = {event => updateName(event)}/>
                                                        <p />
                                                        <Form.Text muted><b>Phone No :</b></Form.Text>
                                                        <Form.Control id="phoneno" name="phoneno" value = {phone}
                                                            placeholder="Phone No*" onChange = {event => updatePhone(event)}/>
                                                        <p />
                                                        <Form.Text muted><b>Address :</b></Form.Text>
                                                        <Form.Control id="address" name="address" value = {address}
                                                            placeholder="Address" onChange = {event => updateAddress(event)}/>
                                                        <p />
                                                        <Form.Text muted><b>Expected Price (INR):</b></Form.Text>
                                                        <Form.Control id="expectedprice" name="expectedprice" value = {expectedprice}
                                                            placeholder="Expected Price" onChange = {event => updateExpectedprice(event)}/>
                                                        <p />
                                                        <Button id="create" type="submit">Save Details</Button>
                                                    </td>
                                                </tr>
                                            </tbody>
                                        </Table>
                                    </td>
                                    <td width="80%">
                                        <Table striped bordered>
                                            <thead>
                                                <tr>                                                
                                                    <th colSpan="6">
                                                        <b>Available Classifieds</b>
                                                        {" "}
                                                        (Count: {classifieds.length})
                                                    </th> 
                                                </tr>
                                            </thead>
                                            <tbody>
                                                <tr>
                                                    <td width="250"><b>Classified Category</b></td>                                                    
                                                    <td width="300"><b>Description</b></td>
                                                    <td width="200"><b>Name</b></td>
                                                    <td width="200"><b>Phone</b></td>                                                    
                                                    <td width="250"><b>Address</b></td>
                                                    <td width="350"><b>Expected Price (INR)</b></td>
                                                </tr> 
                                                {classifieds.map(classified =>
                                                <tr key={classified.id}>
                                                    <td>{classified.category}</td>                                                    
                                                    <td>{classified.description}</td>
                                                    <td>{classified.name}</td>
                                                    <td>{classified.phone}</td>                                                    
                                                    <td>{classified.address}</td>
                                                    <td>{classified.expectedprice}</td>
                                                    {/* <td><img id="CandidatePhoto" src={shortlistCandidate.photo} alt="CandidatePhoto" height="40" width="40"/></td> */}
                                                    {/* <td><Button id='view' onClick={() => ViewDetails(shortlistCandidate.id)}>View Details</Button></td> */}
                                                </tr>)}            
                                            </tbody>    
                                        </Table>
                                    </td>
                                </tr>
                            </tbody>
                        </Table>
                    </Form>
            </div>        
        )
    }
    else
    {
        return (
            <div>
                Loading ...
            </div>
        )
    } 
}

export default Classifieds