import axios from 'axios'
import storage from '../utils/Storage'

const baseUrl = '/api/candidates'

const getConfig = () => {
    return {
      headers: { Authorization: `bearer ${storage.loadUser().token}` }
    }
}

// const sleep = (milliseconds) => {
//   return new Promise(resolve => setTimeout(resolve, milliseconds))
// }

const create = async (candidate) => {
  try {    
    const response = await axios.post(baseUrl, candidate, getConfig())
  
    return response.data
  }
  catch (exception) {
    console.log('exception : ', exception.message)
    return exception
  }    
}

const savePhoto = async (formdata) => {
  try {    
    const response = await axios.post(baseUrl + '/savePhoto', formdata, getConfig())
  
    return response.data
  }
  catch (exception) {
    console.log('exception : ', exception.message)
    return exception
  }    
}

const search = async (education, profession, city, country, gender, birthyear) => {
  try {
    //const searchCriteria = {education, profession, city, country, gender, birthyear}

    const response = await axios.get(`${baseUrl}/${education},${profession},${city},${country},${gender},${birthyear}`)    

    // const response = await axios.get(`${baseUrl}, {'education': ${education}, 'profession': ${profession}, 'city': ${city},
    //                                                        'country': ${country}, 'gender': ${gender}}`)

    return response.data
  }
  catch (exception) {
    console.log('exception : ', exception.message)
    return exception
  }    
}

const getCandidateDetails = async (candidateID) => {
  try {        
    const response = await axios.get(`${baseUrl}/${candidateID}`)    
    
    return response.data
  }
  catch (exception) {
    console.log('exception : ', exception.message)
    return exception
  }    
}

const getAll = async () => {
  try {    
    const response = await axios.get(baseUrl)
    
    return response.data
  }
  catch (exception) {
    console.log('exception : ', exception.message)
    return exception
  }    
}

const updateCandidateDetails = async (candidate, candidateID) => {
  try {
    const response = await axios.put(`${baseUrl}/${candidateID}`, candidate)

    return response.data
  }
  catch (exception) {
    console.log('exception : ', exception.message)
    return exception
  }
}

export default { getAll, create, savePhoto, search, getCandidateDetails, updateCandidateDetails }