import candidateService from '../services/candidateService'

const candidateReducer = (state = [], action) => {
    switch (action.type) {
        case 'NEW_CANDIDATE':
            // console.log('NEW_CANDIDATE : ', action.data)            
            return [...state, action.data]
        case 'SAVE_PHOTO':
            return action.data
        case 'INIT_CANDIDATE':
            // console.log('INIT_CANDIDATE : ', action.data)
            return action.data
        case 'SEARCH_CANDIDATE':            
            // console.log('SEARCH_CANDIDATE : ', action.data)
            return action.data
        case 'CANDIDATE_DETAILS':
            // console.log('CANDIDATE_DETAILS : ', action.data)
            return action.data
        case 'UPDATE_CANDIDATE_DETAILS':
            const updateCandidateId = action.data.id
            const candidateToUpdate = state.find(c => c.id === updateCandidateId)

            const updatedCandidate = {
                ...candidateToUpdate,
                fullname: action.data.fullname,
                gender: action.data.gender,
                photo: action.data.photo,
                marriagestatus: action.data.marriagestatus,
                mobileno: action.data.mobileno,
                birthdate: action.data.birthdate,
                birthplace: action.data.birthplace,
                birthtime: action.data.birthtime,
                horoscope: action.data.horoscope,
                height: action.data.height,
                weight: action.data.weight,
                education: action.data.education,
                profession: action.data.profession,
                fathername: action.data.fathername,
                mothername: action.data.mothername,
                sibling1: action.data.sibling1,
                relation1: action.data.relation1,
                sibling2: action.data.sibling2,
                relation2: action.data.relation2,
                sibling3: action.data.sibling3,
                relation3: action.data.relation3,
                address: action.data.address,
                city: action.data.city,
                country: action.data.country,
                native: action.data.native,
                mosal: action.data.mosal,
                email: action.data.email,
                relative1: action.data.relative1,
                contact1: action.data.contact1,
                relative2: action.data.relative1,
                contact2: action.data.contact1,
                relative3: action.data.relative1,
                contact3: action.data.contact1,
                relative4: action.data.relative1,
                contact4: action.data.contact1,
                relative5: action.data.relative1,
                contact5: action.data.contact1,
                relative6: action.data.relative1,
                contact6: action.data.contact1,
                remarks: action.data.remarks
            }
            return state.map(candidate => candidate.id !== updateCandidateId ? candidate : updatedCandidate)           
        default:
            return state
    }
}

export const initializeCandidates = () => {
    return async dispatch => {        
        const candidates = await candidateService.getAll()
        
        dispatch({
            type: 'INIT_CANDIDATE',
            data: candidates
        })
    }
}

// const sleep = (milliseconds) => {
//     return new Promise(resolve => setTimeout(resolve, milliseconds))
// }

export const newCandidate = (candidateObject) => {

    return async dispatch => {        
        const newCandidate = await candidateService.create(candidateObject)

        // sleep(100000)

        dispatch({
            type:'NEW_CANDIDATE',
            data: newCandidate
        })
    }
}

export const saveCandidatePhoto = (formdata) => {
    return async dispatch => {        
        const savePhoto = await candidateService.savePhoto(formdata)
  
        dispatch({
            type:'SAVE_PHOTO',
            data: savePhoto
        })
    }
}

export const searchCandidate = (education, profession, city, country, gender, birthyear) => {
    return async dispatch => {
        const searchResults = await candidateService.search(education, profession, city, country, gender, birthyear)

        dispatch({
            type: 'SEARCH_CANDIDATE',
            data: searchResults
        })
    }
}

export const getCandidateDetails = (candidateID) => {
    return async dispatch => {
        const candidateDetails =  await candidateService.getCandidateDetails(candidateID)
        
        dispatch({
            type: 'CANDIDATE_DETAILS',
            data: candidateDetails
        })
    }
}

export const updateCandidateDetails = (candidate, candidateID) => {
    return async dispatch => {
        const updateCandidateDetails = await candidateService.updateCandidateDetails(candidate, candidateID)

        dispatch({
            type: 'UPDATE_CANDIDATE_DETAILS',
            data: updateCandidateDetails
        })
    }
}

export default candidateReducer