import React, { useState, useEffect } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { Table, Form, Button } from 'react-bootstrap'
import { initializeCandidates, getCandidateDetails } from '../reducers/candidateReducer'
import { getUserDetails } from '../reducers/userReducer'
import { useHistory, useParams } from 'react-router-dom'
import moment from 'moment'
import candidateService from '../services/candidateService'
import userService from '../services/userService'
import shortlistService from '../services/shortlistService'
import storage from '../utils/Storage'

const Dashboard = () => {

    const dispatch = useDispatch()    
    const history = useHistory()    
    // const [allCandidates, setAllCandidates] = useState([]) 
    const [loggedinCandidate, setLoggedinCandidate] = useState(null) 
    const [loggedinShortListed, setLoggedinShortListed] = useState([])         
    
    let shortlist1 = ""
    let shortlist2 = ""
    let shortlist3 = ""
    let shortlist4 = ""
    let shortlist5 = ""
    // let allCandidates = ""    
    let shortlistCandidates = ""    
    // let loggedinCandidateID = ""   
    let loginID = null 

    // Get all details of loggedin candidate and populate "Candidate Details" section
    let loggedinCandidateID = useParams().id    
    console.log('value of useParams().id : ', useParams().id)
    if (useParams().id === "undefined")
    {
        let user = storage.loadUser() 
        loginID = user.loginid

        let loggedinUser = ""

        setTimeout(()=> {
            loggedinUser = dispatch(getUserDetails(loginID))
            // loggedinUser = userService.getUserDetails(loginID)    
            }, 3000)
        
        let newCandidateID = loggedinUser.candidateid
        console.log('value of new CandidateID : ', newCandidateID)
        loggedinCandidateID = newCandidateID
    }   
    
    useEffect(() => { candidateService.getCandidateDetails(loggedinCandidateID === undefined ? "" : loggedinCandidateID)
        .then(loggedinCandidate => { setLoggedinCandidate(loggedinCandidate) }) }, [loggedinCandidateID])   
  
    // // Get details of all available candidates in application and populate "Available Candidate Details" section
    // useEffect(() => { candidateService.getAll().then(allCandidates => { setAllCandidates(allCandidates) }) }, [])   
    let allCandidates = useEffect(() => { dispatch(initializeCandidates()) },[dispatch, loggedinCandidateID])             
    allCandidates = useSelector(state => state.candidates)
    
    allCandidates = allCandidates.filter(candidate => candidate.id !== loggedinCandidateID)
    
    // Get details of all shortlisted candidates by loggedin candidate and populate "Shortlisted Candidate Details" section    
    useEffect(() => { shortlistService.getShortlistCandidates(loggedinCandidateID === undefined ? "" : loggedinCandidateID)
        .then(loggedinShortListed => { setLoggedinShortListed(loggedinShortListed) }) }, [loggedinCandidateID])                   

    if (loggedinShortListed !== undefined & loggedinShortListed.length !== 0)
    {
        shortlist1 = loggedinShortListed[0].shortlistid
        shortlist2 = loggedinShortListed[1].shortlistid
        shortlist3 = loggedinShortListed[2].shortlistid
        shortlist4 = loggedinShortListed[3].shortlistid
        shortlist5 = loggedinShortListed[4].shortlistid
    }    
    
    shortlistCandidates = useSelector(state => state.candidates)
    
    if (shortlistCandidates !== undefined & shortlistCandidates !== null)
    {
        shortlistCandidates = shortlistCandidates.filter(shortlistCandidate => shortlistCandidate.id === shortlist1 || shortlistCandidate.id === shortlist2 ||
            shortlistCandidate.id === shortlist3 || shortlistCandidate.id === shortlist4 || shortlistCandidate.id === shortlist5)

    }

    const ViewDetails = (candidateID) => {     
        try
        {
            history.push({ pathname: `/ViewCandidateForm/${candidateID}`, data: {loggedinCandidateID, candidateID} })
        }        
        catch(exception)
        {
            console.log(exception)
        }
    }

    const UpdateDetails = (candidateID) => {     
        try
        {
            if (loggedinCandidateID === "undefined")
            {
                window.alert("You need to enter your candidate details first before trying to update it.")
                history.push({ pathname: `/Matrimonialform/${loggedinCandidateID}`, data: {loggedinCandidateID} })
            }
            else
            {
                history.push({ pathname: `/Matrimonialform/${loggedinCandidateID}`, data: {loggedinCandidateID, candidateID} })        
            }
        }        
        catch(exception)
        {
            console.log(exception)
        }
    }
    
    if (loggedinCandidate !== null & shortlistCandidates !== null & allCandidates !== null)
    {          
        return (
            <div className="components"> 
                <Form>
                    <Table striped bordered>
                        <thead>                           
                        </thead>
                        <tbody>
                            <tr>
                                <td>                                  
                                    <Table striped bordered>
                                        <thead>
                                            <tr>
                                                <th colSpan="2"><b>Candidate Profile</b></th>
                                            </tr>
                                        </thead>
                                        <tbody>
                                            <tr>
                                                <td>                                                    
                                                    <Form.Text className="bold-text"><b>Full Name :</b></Form.Text>                                                                    
                                                    <Form.Control id="fullname" name="fullname" value={loggedinCandidate.fullname} readOnly/>                                                    
                                                    <Form.Text className="bold-text"><b>Birth Date :</b></Form.Text>                                                                    
                                                    <Form.Control id="birthdate" name="birthdate" value={moment(loggedinCandidate.birthdate).format('DD-MMM-YYYY')} readOnly/>
                                                </td>
                                                <td>                                                    
                                                    <Form.Text className="bold-text"><b>Education :</b></Form.Text>                                                                    
                                                    <Form.Control id="education" name="education" value={loggedinCandidate.education} readOnly/>                                                    
                                                    <Form.Text className="bold-text"><b>Profession :</b></Form.Text>                                                                    
                                                    <Form.Control id="profession" name="profession" value={loggedinCandidate.profession} readOnly/>
                                                </td> 
                                            </tr>
                                            <tr>                                                                                    
                                                <td>
                                                    <Form.Text className="bold-text"><b>Email :</b></Form.Text>                                                                    
                                                    <Form.Control id="email" name="email" value={loggedinCandidate.email} readOnly/>                                                    
                                                    <Form.Text className="bold-text"><b>Mobile No :</b></Form.Text>                                                                    
                                                    <Form.Control id="mobileno" name="mobileno"  value={loggedinCandidate.mobileno} readOnly/>
                                                </td>
                                                <td>                                                    
                                                    <Form.Text className="bold-text"><b>Height (cms) :</b></Form.Text>                                                                    
                                                    <Form.Control id="height" name="height" value={loggedinCandidate.height} readOnly/>                                                    
                                                    <Form.Text className="bold-text"><b>Weight (kgs) :</b></Form.Text>                                                                    
                                                    <Form.Control id="weight" name="weight" value={loggedinCandidate.weight} readOnly/>
                                                </td>
                                            </tr>
                                            <tr>                                               
                                                <td colSpan="2" align="right">
                                                    <Button id="update" type="submit"  onClick={() => UpdateDetails(loggedinCandidate.id)}>Update Details</Button>                                            
                                                </td>                                            
                                            </tr>                                        
                                        </tbody>
                                    </Table>    
                                </td>
                                <td>                                
                                    <Table striped bordered>
                                        <thead>
                                            <tr>
                                                <th colSpan="9">                                                    
                                                    <b>Shortlisted Candidates Profile</b>
                                                    {" "}
                                                    (Count: {shortlistCandidates.length})                                                    
                                                    {" "}
                                                    (maximum 5 candidates can be shortlisted)                                                    
                                                </th>                                                
                                            </tr>
                                        </thead>
                                        <tbody>    
                                            <tr>
                                                <td width="250" aria-multiline><b>Full Name</b></td>
                                                {/* <td width="200"><b>Gender</b></td> */}
                                                <td width="300"><b>Birth Date</b></td>
                                                <td width="200"><b>Education</b></td>
                                                <td width="200"><b>Profession</b></td>
                                                {/* <td width="150"><b>Height (cms)</b></td>
                                                <td width="150"><b>Weight (kgs)</b></td> */}
                                                <td width="250"><b>Photo</b></td>
                                                <td width="350"><b>View</b></td>
                                            </tr> 
                                            {shortlistCandidates.map(shortlistCandidate =>
                                            <tr key={shortlistCandidate.id}>
                                                <td>{shortlistCandidate.fullname}</td>
                                                {/* <td>{shortlistCandidate.gender}</td> */}
                                                <td>{moment(shortlistCandidate.birthdate).format('DD-MMM-YYYY')}</td>
                                                <td>{shortlistCandidate.education}</td>
                                                <td>{shortlistCandidate.profession}</td>
                                                {/* <td>{shortlistCandidate.height}</td>
                                                <td>{shortlistCandidate.weight}</td> */}
                                                <td><img id="CandidatePhoto" src={shortlistCandidate.photo} alt="CandidatePhoto" height="40" width="40"/></td>
                                                <td><Button id='view' onClick={() => ViewDetails(shortlistCandidate.id)}>View Details</Button></td>
                                            </tr>)}                                             
                                        </tbody>
                                    </Table>                                   
                                </td>
                            </tr>
                            <tr>
                                <td colSpan="2">
                                    <Table striped bordered>
                                        <thead>
                                            <tr>                                                
                                                <th colSpan="9">
                                                    <b>Available Candidate Details</b>
                                                    {" "}
                                                    (Count: {allCandidates.length})
                                                </th> 
                                            </tr>
                                        </thead>
                                        <tbody>
                                        <tr>
                                            <td width="250" aria-multiline><b>Full Name</b></td>
                                            <td width="200"><b>Gender</b></td>
                                            <td width="300"><b>Birth Date</b></td>
                                            <td width="200"><b>Education</b></td>
                                            <td width="200"><b>Profession</b></td>
                                            <td width="150"><b>Height (cms)</b></td>
                                            <td width="150"><b>Weight (kgs)</b></td>
                                            <td width="250"><b>Photo</b></td>
                                            <td width="350"><b>View</b></td>
                                        </tr>
                                        {allCandidates.map(candidate =>
                                        <tr key={candidate.id}>
                                            <td>{candidate.fullname}</td>
                                            <td>{candidate.gender}</td>
                                            <td>{moment(candidate.birthdate).format('DD-MMM-YYYY')}</td>
                                            <td>{candidate.education}</td>
                                            <td>{candidate.profession}</td>
                                            <td>{candidate.height}</td>
                                            <td>{candidate.weight}</td>
                                            <td><img id="CandidatePhoto" src={candidate.photo} alt="CandidatePhoto" height="100" width="100"/></td>
                                            <td><Button id='view' onClick={() => ViewDetails(candidate.id)}>View Details</Button></td>
                                        </tr>)}
                                        </tbody>
                                    </Table>                                
                                </td>    
                            </tr>                                    
                        </tbody>
                    </Table>
                </Form>
            </div>
        )
    }
    else
    {
        return (
            <div>
                Loading ...
            </div>
        )
    }  
}

export default Dashboard