import axios from 'axios'
import storage from '../utils/Storage'

const baseUrl = '/api/shortlists'

const getConfig = () => {
    return {
      headers: { Authorization: `bearer ${storage.loadUser().token}` }
    }
}

const getShortlistCandidates = async (candidateID) => {
    try {
        const response = await axios.get(`${baseUrl}/${candidateID}`)
        
        return response.data
    }
    catch (exception) {
        console.log('exception : ', exception.message)
        return exception
    }  
}

const createShortlistCandidates = async (shortlists) => {
    try {
        const request = axios.post(baseUrl, shortlists, getConfig())

        return request.then(response => response.data)
    }
    catch (exception) {
        console.log('exception : ', exception.message)
        return exception
    }  
}

const updateShortlistCandidates = async (shortlists) => {
    try {
        const request = axios.put(`${baseUrl}/${shortlists.id}`, shortlists, getConfig())

        return request.then(response => response.data)
    }
    catch (exception) {
        console.log('exception : ', exception.message)
        return exception
    }  
}

const deleteShortlistCandidates = async (id) => {
    try {
        const request = axios.delete(`${baseUrl}/${id}`, getConfig())

        return request.then(response => response.data)
    }
    catch (exception) {
        console.log('exception : ', exception.message)
        return exception
    }  
}

export default { getShortlistCandidates, createShortlistCandidates, updateShortlistCandidates, deleteShortlistCandidates }