import React, { useState, useEffect } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { useHistory, useParams } from 'react-router-dom'
import { Table, Form, Button } from 'react-bootstrap'
import { initializeCandidates, getCandidateDetails } from '../reducers/candidateReducer'

const DirectoryForm = () => {

    const dispatch = useDispatch()    

    const loggedinCandidateID = useParams().id

    let allCandidates = useEffect(() => { dispatch(initializeCandidates()) },[dispatch, loggedinCandidateID])             
    allCandidates = useSelector(state => state.candidates)

    if (allCandidates !== null)
    { 
        return (
            <div className="components"> 
                    <Form>
                        <Table striped bordered>
                            <thead>
                                <tr>                                                
                                    <th colSpan="9">
                                        <b>Available Members</b>
                                        {" "}
                                        (Count: {allCandidates.length})
                                    </th> 
                                </tr>
                            </thead>
                            <tbody>                            
                                <tr>
                                    <td width="250" aria-multiline><b>Family Head Name</b></td>
                                    <td width="200"><b>Spouse Name</b></td>
                                    <td width="300"><b>Address</b></td>
                                    <td width="200"><b>Phone No</b></td>                                    
                                </tr>
                                {allCandidates.map(candidate =>
                                <tr key={candidate.id}>
                                    <td>{candidate.fathername}</td>
                                    <td>{candidate.mothername}</td>
                                    <td>{candidate.address}</td>
                                    <td>{candidate.mobileno}</td>
                                </tr>)}
                            </tbody>
                        </Table>
                    </Form>
                </div>
        )
    }
    else
    {
        return (
            <div>
                Loading ...
            </div>
        )
    }  
}

export default DirectoryForm