import React, { useState, useEffect } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { Table, Form, Button } from 'react-bootstrap'
import { useHistory } from 'react-router-dom'
import { useParams } from 'react-router-dom'
import Select from 'react-select'
import { initializeCandidates, searchCandidate } from '../reducers/candidateReducer'
import initializeService from '../services/initializeService'
import DatePicker from 'react-datepicker'
import 'react-datepicker/dist/react-datepicker.css'
import storage from '../utils/Storage'
import moment from 'moment'

const SearchForm = () => {

    const [educations, setEducations] = useState([]) 
    const [professions, setProfessions] = useState([]) 
    const [cities, setCities] = useState([]) 
    const [countries, setCountries] = useState([]) 
    const [genders, setGenders] = useState([]) 
    
    const [selectedEducation, setSelectedEducation] = useState(null);
    const [selectedProfession, setSelectedProfession] = useState(null);
    const [selectedCity, setSelectedCity] = useState(null);
    const [selectedCountry, setSelectedCountry] = useState(null);
    const [selectedGender, setSelectedGender] = useState(null);
    const [selectedBirthYear, setSelectedBirthYear] = useState(null);

    const dispatch = useDispatch()
    const history = useHistory()    
    let loggedinCandidateID = useParams().id 
    let availableCandidateCount = 0

    let searchResults = []    

    let searchCandidates = useEffect(() => { dispatch(initializeCandidates()) },[dispatch])        
    searchCandidates = useSelector(state => state.candidates)            
    
    if (loggedinCandidateID !== null || loggedinCandidateID !== undefined)
    {
        let user = storage.loadUser() 

        if (user !== null & user !== undefined)
        {
            loggedinCandidateID = user.candidateid    
        }
    }
    
    searchCandidates = searchCandidates.filter(candidate => candidate.id !== loggedinCandidateID)
    
    availableCandidateCount = storage.loadCandidateCount()

    if (availableCandidateCount === null & availableCandidateCount === 0)
    {
        availableCandidateCount = searchCandidates.length
        storage.saveCandidateCount(availableCandidateCount)
    }

    useEffect(() => { initializeService.getEducation()
        .then(educations => { setEducations(educations) }) }, [])    

    useEffect(() => { initializeService.getProfession()
        .then(professions => { setProfessions(professions) }) }, [])

    useEffect(() => { initializeService.getCity()
        .then(cities => { setCities(cities) }) }, [])

    useEffect(() => { initializeService.getCountry()
        .then(countries => { setCountries(countries) }) }, [])

    useEffect(() => { initializeService.getGender()
        .then(genders => { setGenders(genders) }) }, [])

    const BackToDashboard = (candidateID) => {     
        try
        {
            if (candidateID === null || candidateID === undefined)
            {
                let user = storage.loadUser() 

                if (user !== null & user !== undefined)
                {
                    candidateID = user.candidateid    
                }
            }

            history.push({ pathname: `/dashboard/${candidateID}`, data: {id: candidateID} })
        }
        catch(exception)
        {
            console.log(exception)
        }
    }

    const searchRecords = (event) => {
        try
        {
            event.preventDefault()
      
            searchResults = dispatch(searchCandidate(selectedEducation === null ? "ALL" : selectedEducation.value, 
                                                    selectedProfession === null ? "ALL" : selectedProfession.value, 
                                                    selectedCity === null ? "ALL" : selectedCity.value,
                                                    selectedCountry === null ? "ALL" : selectedCountry.value, 
                                                    selectedGender === null ? "ALL" : selectedGender.value, 
                                                    selectedBirthYear === null ? "ALL" : selectedBirthYear.getFullYear()))     
                                                    
            console.log('value of searchResults : ', searchResults)
        }        
        catch(exception)
        {
            console.log(exception)
        }
    }

    const viewCandidateDetails = (candidateID) => {
        try
        {
            history.push({ pathname: '/ViewCandidateForm', data: {loggedinCandidateID, candidateID} })
        }        
        catch(exception)
        {
            console.log(exception)
        }
    }
    
    if (searchCandidates !== null & searchCandidates !== undefined)
    {
        availableCandidateCount = storage.loadCandidateCount() 

        return (
            <div className="components">                
                <Form onSubmit={searchRecords}>
                    <Table striped bordered>
                        <thead>
                            <tr>
                                <th colSpan="3"><b>Available Profiles</b>
                                    {" "}
                                    (Count: {availableCandidateCount})
                                </th> 
                            </tr>
                        </thead>
                        <tbody>     
                            <tr>     
                                <td> 
                                    <tr>  
                                        <td><Form.Label><b>Education:</b></Form.Label></td>
                                        <td width="250"><Select options={educations} placeholder="Education" value={selectedEducation}
                                            onChange={setSelectedEducation} defaultValue={{ label: 'All', value: 'ALL' }}/></td>
                                    </tr>
                                    <tr>
                                        <td><Form.Label><b>Profession:</b></Form.Label></td>
                                        <td><Select options={professions} placeholder="Profession" value ={selectedProfession}
                                            onChange={setSelectedProfession} defaultValue={{ label: 'All', value: 'ALL' }}/></td>
                                    </tr>                                                                
                                </td>                                     
                                <td>
                                    <tr>
                                        <td><Form.Label><b>Gender:</b></Form.Label></td>
                                        <td><Select options={genders} placeholder="Gender" value ={selectedGender}
                                            onChange={setSelectedGender} defaultValue={{ label: 'All', value: 'ALL' }}/></td>                                    
                                    </tr> 
                                    <tr>
                                        <td><Form.Label><b>Birth Year:</b></Form.Label></td>
                                        <td><DatePicker selected={selectedBirthYear} onChange={setSelectedBirthYear} showYearPicker /></td>                                        
                                        {/* <td><DatePicker selected={selectedBirthYear} onChange={date => setSelectedBirthYear(date)} showYearPicker openToDate={new Date("2000/01/01")}/></td>                                        */}
                                    </tr>                                
                                </td>
                                <td>
                                    <tr>
                                        <td><Form.Label><b>City:</b></Form.Label></td>
                                        <td width="250"><Select options={cities} placeholder="City" value={selectedCity}
                                            onChange={setSelectedCity} defaultValue={{ label: 'All', value: 'ALL' }}/></td>                                    
                                    </tr>
                                    <tr>
                                        <td><Form.Label><b>Country:</b></Form.Label></td>
                                        <td><Select options={countries} placeholder="Country" value={selectedCountry}
                                            onChange={setSelectedCountry}  defaultValue={{ label: 'All', value: 'ALL' }}/></td>                                    
                                    </tr>    
                                </td> 
                            </tr>                             
                            <tr>
                                <td colSpan="3" align="right"><Button id="search" type="submit">Search Profiles</Button>
                                {" "}
                                <Button id="back" onClick={() => BackToDashboard(loggedinCandidateID)}>Back</Button>
                                </td>
                            </tr>   
                            </tbody>                         
                        </Table>                                      
                    </Form>
                    <Table striped bordered>
                        <thead>
                            <tr>
                                <th colSpan="9"><b>Search Results</b>
                                    {" "}
                                    (Count: {searchCandidates.length})
                                </th> 
                            </tr>
                        </thead>
                        <tbody>                        
                            <tr>
                                <td width="250" aria-multiline><b>Full Name</b></td>
                                <td width="200"><b>Gender</b></td>
                                <td width="300"><b>Birth Date</b></td>
                                <td width="200"><b>Education</b></td>
                                <td width="200"><b>Profession</b></td>
                                <td width="150"><b>Height (cms)</b></td>
                                <td width="150"><b>Weight (kgs)</b></td>
                                <td width="250"><b>Photo</b></td>
                                <td width="350"><b>View</b></td>
                            </tr>
                            {searchCandidates.map(searchCandidate =>
                            <tr key={searchCandidate.id}>
                                <td>{searchCandidate.fullname}</td>
                                <td>{searchCandidate.gender}</td>                                
                                <td>{moment(searchCandidate.birthdate).format('DD-MMM-YYYY')}</td>
                                <td>{searchCandidate.education}</td>
                                <td>{searchCandidate.profession}</td>
                                <td>{searchCandidate.height}</td>
                                <td>{searchCandidate.weight}</td>
                                <td><img id="CandidatePhoto" src={searchCandidate.photo} alt="CandidatePhoto" height="100" width="100"/></td>
                                <td><Button id='view' onClick={() => viewCandidateDetails(searchCandidate.id)}>View Details</Button></td>
                            </tr>)} 
                        </tbody>
                    </Table>   
            </div>    
        )
    }
    else
    {
        return (
            <div>
                Loading ...
            </div>
        )
    }
}

export default SearchForm